import React from 'react';
import style from './carouselChild.module.css';

const Child = ({ city }) => {
  const { logo, name, text = [], background, button } = city;
  return (
    <div className={style.slide}>
      <div className={style.wrapperLogo}>
        <img className={style.logo} src={logo} alt="City Logo" />
      </div>
      <div className={style.textContainer}>
        <div
          style={{
            position: 'absolute',
            width: '100%',
            height: '100%',
            backgroundImage: `url(${background})`,
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            backgroundRepeat: 'no-repeat',
          }}
        >
          <h3 className={style.city}>{name}</h3>
          {text.map(enter => (
            <div className={style.wrapperList} key={enter.title}>
              <h4 className={style.direction}>{enter.title}</h4>
              <ul className={style.directionList}>
                {enter.list.map(listItem => (
                  <li className={style.directionListItem} key={listItem}>
                    <span className={style.directionListItemText}>
                      {listItem}
                    </span>
                  </li>
                ))}
              </ul>
              {button && (
                <button type="button" className={style.buttonItem}>
                  подключить
                </button>
              )}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Child;
